import {
  Component,
  ElementRef,
  Input,
  OnChanges,
  OnInit,
  ViewChild,
} from '@angular/core';
import FileSaver from 'file-saver';
import html2canvas from 'html2canvas';
import * as jspdf from 'jspdf';
import jsonToCsv from './jsonToCsv';

@Component({
  selector: 'app-datagrid',
  templateUrl: './datagrid.component.html',
  styleUrls: ['./datagrid.component.css'],
})
export class DatagridComponent implements OnInit, OnChanges {
  @ViewChild('table') table: ElementRef;

  @Input()
  data = [];
  headers = [];
  constructor() {}

  ngOnInit() {
    // console.log('ngOnInit');
    // console.log(this.data);
  }

  ngOnChanges() {
    // console.log('ngOnChanges');
    // console.log(this.data);
    // this.headers =
    //   this.data && this.data.length > 0 ? Object.keys(this.data[0]) : [];
    try {
      this.headers = Object.keys(this.data[0]);
    } catch (error) {}
  }

  downloadcsv() {
    const csv = jsonToCsv([this.headers, ...this.data]);
    const blob = new Blob([csv], {
      type: 'text/csv',
    });
    FileSaver.saveAs(blob, `report-${Date.now().toString(36)}.csv`);
  }

  downloadpdf() {
    const domelement = this.table.nativeElement;

    html2canvas(domelement).then(canvas => {
      const contentDataURL = canvas.toDataURL('image/png');
      const pdf = new jspdf('p', 'mm', 'a4'); // A4 size page of PDF

      const imgWidth = 180;
      const imgHeight = (canvas.height * imgWidth) / canvas.width;

      pdf.addImage(contentDataURL, 'PNG', 15, 10, imgWidth, imgHeight);
      pdf.save(`report-${Date.now().toString(36)}.pdf`);
    });
  }
}
