import { Component, Input } from '@angular/core';
import * as d3 from 'd3';
import { select } from 'd3-selection';
import { dimpleColors, renderLegend, renderTitle, renderTtip } from '../charts';
import { AbstractChartComponent } from '../charts.component';

@Component({
  selector: 'app-pie',
  templateUrl: '../charts.component.html',
  styleUrls: ['../charts.component.css']
})
export class PieComponent extends AbstractChartComponent {
  @Input() title = 'Pie Chart';

  renderChart(titleText, data, containerElement) {
    renderChart(titleText, data, containerElement);
  }
}

function renderChart(titleText, data, containerElement) {
  if (!data) {
    return;
  }
  const container = select(containerElement);
  const categories = data.map(r => r.category);
  const { width, height } = containerElement.getBoundingClientRect();
  const radius = Math.min(width, height) / 2;
  const pie = d3.pie().value((d: any) => d.value);
  const arc = d3
    .arc()
    .innerRadius(1)
    .outerRadius(radius - 50)
    .padAngle(0.1)
    .padRadius(10)
    .cornerRadius(5);
  const labelArc = d3
    .arc()
    .innerRadius(radius - 60)
    .outerRadius(radius - 50);
  const layers = pie(data);

  const colorScale = d3.scaleOrdinal(dimpleColors);

  const svg = container
    .append('svg')
    .style('border', '1px solid #ccc')
    .style('font-family', 'sans-serif')
    .style('font-size', '12px')
    .attr('width', width)
    .attr('height', height);

  const chart = svg
    .append('g')
    .attr('transform', `translate(${width / 2},${height / 2})`);

  // console.log(layers[0]);
  // console.log(arc(layers[0]));

  chart
    .selectAll('path.slice')
    .data(layers)
    .enter()
    .append('path')
    .classed('slice', true)
    .attr('d', (d: any) => arc(d))
    .attr('fill', (d: any) => colorScale(d.data.category))
    .attr('stroke', '#ccc');

  const label = chart
    .selectAll('g.label')
    .data(layers)
    .enter()
    .append('g')
    .classed('chartlabel', true)
    .style('text-anchor', 'middle')
    .style('pointer-events', 'none')
    .attr('transform', (d: any) => `translate(${labelArc.centroid(d)})`);

  label
    .append('text')
    .attr('dy', '-0.5em')
    .text((d: any) => d.data.category);
  label
    .append('text')
    .attr('dy', '0.35em')
    .text((d: any) => d.data.value);

  svg
    .selectAll('path.slice')
    .on('mousemove', (d: any, i, j) => {
      // @ts-ignore
      const mouse = d3.mouse(svg.node());
      ttip
        .style('opacity', 0.9)
        .style('left', `${mouse[0]}px`)
        // .style('right', `${width - mouse[0]}px`)
        .style('bottom', `${height - mouse[1]}px`)
        .style('background', colorScale(d.data.category))
        .html(Object.values(d.data).join('<br>'));
    })
    .on('mouseleave', function() {
      ttip.style('opacity', 0);
    });

  // tooltip
  const ttip = renderTtip(container);

  // title
  renderTitle(container, titleText);

  // legend
  renderLegend(container, categories, colorScale);
}
