import { Component, OnInit } from '@angular/core';
import { LocalDate } from 'js-joda';
import { share } from 'rxjs/operators';
import { ApiService } from '../../../shared/api.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css'],
})
export class DashboardComponent implements OnInit {
  form = {
    startDate: LocalDate.now()
      .minusDays(7)
      .toString(),
    endDate: LocalDate.now()
      .plusDays(1)
      .toString(),
  };

  data0$;
  data1$;
  data2$;
  data2c$;
  data2s$;

  constructor(private api: ApiService) {}

  ngOnInit() {}

  generate(form) {
    this.data0$ = this.api.get(`/api4/dashboard/r0`).pipe(share());
    this.data1$ = this.api.get(`/api4/dashboard/r1`).pipe(share());
    this.data2$ = this.api.get(`/api4/dashboard/r2`).pipe(share());
    this.data2c$ = this.api.get(`/api4/dashboard/r2c`).pipe(share());
    this.data2s$ = this.api.get(`/api4/dashboard/r2s`).pipe(share());
  }
}
