import { Component, Input } from '@angular/core';
import * as d3 from 'd3';
import { max } from 'd3-array';
import { select } from 'd3-selection';
import uniq from 'lodash-es/uniq';
import { dimpleColors, renderLegend, renderTitle, renderTtip } from '../charts';
import { AbstractChartComponent } from '../charts.component';

@Component({
  selector: 'app-spider',
  templateUrl: '../charts.component.html',
  styleUrls: ['../charts.component.css']
})
export class SpiderComponent extends AbstractChartComponent {
  @Input() title = 'Spider Chart';

  renderChart(titleText, data, containerElement) {
    renderChart(titleText, data, containerElement);
  }
}

function renderChart(titleText, data, containerElement) {
  if (!data) {
    return;
  }
  const container = select(containerElement);
  const dataMax = +max(data.map(d => d.value));
  const categories = uniq(data.map(r => r.category));
  const series = uniq(data.map(r => r.series));
  const { width, height } = containerElement.getBoundingClientRect();
  const layers = series.map(_seria => data.filter(d => d.series === _seria));

  const colorScale = d3.scaleOrdinal(dimpleColors);
  const xScale = d3
    .scalePoint()
    .domain(categories)
    .range([0, Math.PI * 2])
    .padding(0.5);
  const radius = Math.min(width, height) / 2;
  const yScale = d3
    .scaleLinear()
    .domain([0, dataMax])
    .range([0, 0.7 * radius]);

  const svg = container
    .append('svg')
    .style('border', '1px solid #ccc')
    .style('font-family', 'sans-serif')
    .style('font-size', '12px')
    .attr('width', width)
    .attr('height', height);

  const chart = svg
    .append('g')
    .attr('transform', `translate(${width / 2},${height / 2})`);

  // chart
  //   .append('circle')
  //   .attr('r', yScale.range()[1])
  //   .attr('fill', 'none')
  //   .style('stroke', '#ddd');

  for (const category of categories) {
    const g = chart
      .append('g')
      .attr('transform', `rotate(${(xScale(category) * 180) / Math.PI})`);

    g.append('line')
      .attr('y2', -yScale.range()[1])
      .style('stroke', '#ddd');

    g.append('text')
      .style('text-anchor', 'middle')
      // .attr('y', -yScale.range()[1])
      // .attr('dy', -8)
      .attr(
        'transform',
        `translate(0 ${-yScale.range()[1] - 10}) rotate(${(-xScale(category) *
          180) /
          Math.PI})`
      )
      .text(category);
  }

  const line = d3
    .lineRadial()
    .curve(d3.curveLinearClosed)
    // @ts-ignore
    .angle(d => xScale(d.category))
    // @ts-ignore
    .radius(d => yScale(d.value));

  chart
    .selectAll('path.line')
    .data(layers)
    .enter()
    .append('path')
    .classed('line', true)
    .attr('fill', (d, i) => colorScale(series[i]))
    .attr('fill-opacity', 0.2)
    .attr('stroke', (d, i) => colorScale(series[i]))
    .attr('stroke-linejoin', 'round')
    .attr('stroke-linecap', 'round')
    .attr('stroke-width', 1.5)
    .attr('d', line);

  chart
    .selectAll('text.chartlabel')
    .data(data)
    .enter()
    .append('text')
    .classed('chartlabel', true)
    .style('text-anchor', 'middle')
    .attr(
      'transform',
      (i: any) => `rotate(${(xScale(i.category) * 180) / Math.PI})`
    )
    .attr('y', (i: any) => -yScale(i.value))
    .attr('dy', 2)
    .text((i: any) => i.value)
    .on('mousemove', function(d, i, j) {
      // @ts-ignore
      const mouse = d3.mouse(svg.node());
      ttip
        .style('opacity', 0.9)
        .style('left', `${mouse[0]}px`)
        .style('bottom', `${height - mouse[1]}px`)
        // @ts-ignore
        .style('background', `${colorScale(d.series)}`)
        .html(Object.values(d).join('<br>'));
    })
    .on('mouseleave', function() {
      ttip.style('opacity', 0);
    });

  // tooltip
  const ttip = renderTtip(container);

  // title
  renderTitle(container, titleText);

  // legend
  renderLegend(container, series, colorScale);
}
function polar2cartesian(R, theta) {
  return {
    x: R * Math.cos(theta),
    y: R * Math.sin(theta)
  };
}

function cartesian2polar(x, y) {
  return {
    R: Math.sqrt(x * x + y * y),
    theta: Math.atan2(y, x)
  };
}

