import { Component, Input } from '@angular/core';
import * as d3 from 'd3';
import { max } from 'd3-array';
import { select } from 'd3-selection';
import uniq from 'lodash-es/uniq';
import { dimpleColors, renderLegend, renderTitle, renderTtip } from '../charts';
import { AbstractChartComponent } from '../charts.component';

@Component({
  selector: 'app-line',
  templateUrl: '../charts.component.html',
  styleUrls: ['../charts.component.css']
  // encapsulation: ViewEncapsulation.None
})
export class LinechartComponent extends AbstractChartComponent {
  @Input()
  title = 'Line Chart';

  renderChart(titleText, data, containerElement) {
    renderChart(titleText, data, containerElement);
  }
}

function renderChart(titleText, data, containerElement) {
  if (!data) {
    return;
  }
  const container = select(containerElement);
  const dataMax = +max(data.map(d => d.value));
  const categories = uniq(data.map(r => r.category));
  const series = uniq(data.map(r => r.series));
  const { width, height } = containerElement.getBoundingClientRect();
  const layers = series.map(_seria => data.filter(d => d.series === _seria));

  const colorScale = d3.scaleOrdinal(dimpleColors);
  const xScale = d3
    .scalePoint()
    .domain(categories)
    .rangeRound([50, width - 50])
    .padding(0.1);
  const yScale = d3
    .scaleLinear()
    .domain([0, dataMax])
    .range([height - 80, 50]);

  const svg = container
    .append('svg')
    .style('border', '1px solid #ccc')
    .style('font-family', 'sans-serif')
    .style('font-size', '12px')
    .attr('width', width)
    .attr('height', height);

  svg
    .append('g')
    .classed('y-axis', true)
    .attr('transform', `translate(${xScale.range()[0]},0)`)
    .call(d3.axisLeft(yScale));

  svg
    .append('g')
    .classed('x-axis', true)
    .attr('transform', `translate(0,${yScale.range()[0]})`)
    .call(d3.axisBottom(xScale));

  svg
    .selectAll('g.y-axis g.tick')
    .append('line')
    .style('stroke', '#ddd')
    .attr('x1', 0)
    .attr('y1', 0)
    .attr('x2', xScale.range()[1] - xScale.range()[0])
    .attr('y2', 0);

  svg
    .selectAll('g.x-axis g.tick text')
    .attr('text-anchor', 'end')
    .attr('alignment-baseline', 'baseline')
    .attr('dx', '-5')
    .attr('transform', 'rotate(-30)');
  // .attr('transform', 'translate(-5, 0) rotate(-30)');

  const line = d3
    .line()
    // .curve(d3.curveMonotoneX)
    // @ts-ignore
    .x(d => xScale(d.category))
    // @ts-ignore
    .y(d => yScale(d.value));

  svg
    .selectAll('path.line')
    .data(layers)
    .enter()
    .append('path')
    .classed('line', true)
    .attr('fill', 'none')
    .attr('stroke', (d, i) => colorScale(series[i]))
    .attr('stroke-linejoin', 'round')
    .attr('stroke-linecap', 'round')
    .attr('stroke-width', 1.5)
    .attr('d', line);

  svg
    .selectAll('text.chartlabel')
    .data(data)
    .enter()
    .append('text')
    .classed('chartlabel', true)
    .style('text-anchor', 'middle')
    // @ts-ignore
    .attr('x', d => xScale(d.category))
    // @ts-ignore
    .attr('y', d => yScale(d.value))
    // @ts-ignore
    .text(d => d.value)
    .on('mousemove', function(d, i, j) {
      // @ts-ignore
      const mouse = d3.mouse(svg.node());
      ttip
        .style('opacity', 0.9)
        .style('left', `${mouse[0]}px`)
        .style('bottom', `${height - mouse[1]}px`)
        // @ts-ignore
        .style('background', `${colorScale(d.series)}`)
        .html(Object.values(d).join('<br>'));
    })
    .on('mouseleave', function() {
      ttip.style('opacity', 0);
    });

  // https://itnext.io/d3-js-in-all-its-glory-7066601aa16b
  // const voronoi = d3
  //   .voronoi()
  //   .x((d: any) => xScale(d.category))
  //   .y((d: any) => yScale(d.value))
  //   .extent([
  //     [min(xScale.range()), min(yScale.range())],
  //     [max(xScale.range()), max(yScale.range())]
  //   ]);
  // svg
  //   .append('g')
  //   .attr('class', 'voronoi')
  //   .attr('fill', 'rgba(0, 0, 0, 0)')
  //   .selectAll('path')
  //   .data(voronoi.polygons(data))
  //   .enter()
  //   .append('path')
  //   .attr('d', d => (d ? `M${d.join('L')}Z` : null))
  //   .on('mousemove', function(d, i, j) {
  //     // @ts-ignore
  //     const mouse = d3.mouse(svg.node());
  //     ttip
  //       .style('opacity', 0.9)
  //       // @ts-ignore
  //       .style('left', `${xScale(d.data.category)}px`)
  //       // @ts-ignore
  //       .style('bottom', `${height - yScale(d.data.value)}px`)
  //       // @ts-ignore
  //       .style('background', `${colorScale(d.data.series)}`)
  //       .html(Object.values(d.data).join('<br>'));
  //   })
  //   .on('mouseleave', function(d, i, j) {
  //     ttip.style('opacity', 0);
  //   });

  // tooltip
  const ttip = renderTtip(container);

  // title
  renderTitle(container, titleText);

  // legend
  renderLegend(container, series, colorScale);
}
