import { Component, Input } from '@angular/core';
import * as d3 from 'd3';
import { max } from 'd3-array';
import { select } from 'd3-selection';
import { dimpleColors, renderLegend, renderTitle, renderTtip } from '../charts';
import { AbstractChartComponent } from '../charts.component';

@Component({
  selector: 'app-bar',
  templateUrl: '../charts.component.html',
  styleUrls: ['../charts.component.css']
})
export class BarchartComponent extends AbstractChartComponent {
  @Input() title = 'Bar Chart';
  renderChart(titleText, data, containerElement) {
    renderChart(titleText, data, containerElement);
  }
}

function renderChart(titleText, data, containerElement) {
  if (!data) {
    return;
  }
  const container = select(containerElement);
  const dataMax = +max(data.map(d => d.value));
  const categories = data.map(r => r.category);
  const { width, height } = containerElement.getBoundingClientRect();

  const colorScale = d3.scaleOrdinal(dimpleColors);
  const xScale = d3
    .scaleBand()
    .domain(categories)
    .rangeRound([50, width - 50])
    .padding(0.1);
  const yScale = d3
    .scaleLinear()
    .domain([0, dataMax])
    .range([height - 80, 50]);

  const svg = container
    .append('svg')
    .style('border', '1px solid #ccc')
    .style('font-family', 'sans-serif')
    .style('font-size', '12px')
    .attr('width', width)
    .attr('height', height);

  svg
    .append('g')
    .classed('y-axis', true)
    .attr('transform', `translate(${xScale.range()[0]},0)`)
    .call(d3.axisLeft(yScale));

  svg
    .append('g')
    .classed('x-axis', true)
    .attr('transform', `translate(0,${yScale.range()[0]})`)
    .call(d3.axisBottom(xScale));

  svg
    .selectAll('g.y-axis g.tick')
    .append('line')
    .style('stroke', '#ddd')
    .attr('x1', 0)
    .attr('y1', 0)
    .attr('x2', xScale.range()[1] - xScale.range()[0])
    .attr('y2', 0);

  svg
    .selectAll('g.x-axis g.tick text')
    .attr('text-anchor', 'end')
    .attr('alignment-baseline', 'baseline')
    .attr('dx', '-5')
    .attr('transform', 'rotate(-30)');
  // .attr('transform', 'translate(-5, 0) rotate(-30)');

  svg
    .selectAll('rect.bar')
    .data(data)
    .enter()
    .append('rect')
    .classed('bar', true)
    .style('outline', '1px solid #888')
    // @ts-ignore
    .style('fill', d => colorScale(d.category))
    // @ts-ignore
    .attr('x', d => xScale(d.category))
    // @ts-ignore
    .attr('y', d => yScale(d.value))
    // @ts-ignore
    .attr('height', d => yScale(0) - yScale(d.value))
    .attr('width', xScale.bandwidth());

  svg
    .selectAll('rect.bar')
    .on('mousemove', function(d, i, j) {
      // @ts-ignore
      const mouse = d3.mouse(svg.node());
      ttip
        .style('opacity', 0.9)
        .style('left', `${mouse[0]}px`)
        .style('bottom', `${height - mouse[1]}px`)
        // @ts-ignore
        .style('background', `${colorScale(d.category)}`)
        .html(Object.values(d).join('<br>'));
    })
    .on('mouseleave', function() {
      ttip.style('opacity', 0);
    });

  svg
    .selectAll('text.chartlabel')
    .data(data)
    .enter()
    .append('text')
    .classed('chartlabel', true)
    .style('text-anchor', 'middle')
    // @ts-ignore
    .attr('x', d => xScale(d.category) + xScale.bandwidth() / 2)
    // @ts-ignore
    .attr('y', d => yScale(d.value))
    // @ts-ignore
    .text(d => d.value);

  // tooltip
  const ttip = renderTtip(container);

  // title
  renderTitle(container, titleText);

  // legend
  renderLegend(container, categories, colorScale);
}
