import {
  Directive,
  ElementRef,
  EventEmitter,
  Input,
  Output,
} from '@angular/core';
import * as $ from 'jquery';
import 'jquery-ui/ui/widgets/datepicker.js';

@Directive({
  selector: '[appDatepicker][ngModel]',
})
export class DatepickerDirective {
  @Input()
  ngModel: string;
  @Output()
  ngModelChange = new EventEmitter();

  constructor(private el: ElementRef) {
    ($(this.el.nativeElement) as any).datepicker({
      dateFormat: 'yy-mm-dd',
      onSelect: (dateText, inst) => {
        this.valueChanged(dateText);
      },
      onClose: (dateText, inst) => {
        this.valueChanged(dateText);
      },
      // showButtonPanel: true,
      constrainInput: false,
      changeMonth: true,
      changeYear: true,
      showMonthAfterYear: true,
      showOtherMonths: true,
      selectOtherMonths: true,
    });
  }

  valueChanged(dateText) {
    this.ngModelChange.emit(dateText);
  }

  // @HostListener('click')
  // public clickInside() {
  //   console.log('CLicked !');
  //   // this.appDatepicker.open();
  // }

  // @HostListener('document:click', ['$event'])
  // public clickOutside($event: MouseEvent) {
  //   this.appDatepicker.close();
  // }
}
