import {
  AfterContentInit,
  AfterViewInit,
  ElementRef,
  Input,
  OnInit,
  ViewChild,
} from '@angular/core';
import {
  OnChanges,
  OnDestroy,
} from '@angular/core/src/metadata/lifecycle_hooks';
import FileSaver from 'file-saver';
import html2canvas from 'html2canvas';
import * as jspdf from 'jspdf';
import jsonToCsv from './jsonToCsv';

export abstract class AbstractChartComponent
  implements OnInit, AfterViewInit, AfterContentInit, OnChanges, OnDestroy {
  @ViewChild('container') container: ElementRef;
  @Input() data = [];
  @Input() title = 'Abstract Chart';

  constructor() {}

  ngOnInit() {}

  ngAfterViewInit() {}

  ngAfterContentInit() {}

  ngOnChanges() {
    this.container.nativeElement.innerHTML = '';
    this.renderChart(this.title, this.data, this.container.nativeElement);
  }

  ngOnDestroy() {}

  abstract renderChart(titleText, data, containerElement);

  downloadcsv() {
    const headers = Object.keys(this.data[0]);
    const csv = jsonToCsv([headers, ...this.data]);
    const blob = new Blob([csv], {
      type: 'text/csv',
    });
    FileSaver.saveAs(blob, 'report.csv');
  }

  downloadpdf() {
    const data = this.container.nativeElement;

    html2canvas(data).then(canvas => {
      const imgWidth = 180;
      const imgHeight = (canvas.height * imgWidth) / canvas.width;
      const contentDataURL = canvas.toDataURL('image/png');
      const pdf = new jspdf('p', 'mm', 'a4'); // A4 size page of PDF

      pdf.addImage(contentDataURL, 'PNG', 15, 10, imgWidth, imgHeight);
      pdf.save(`report-${Date.now().toString(36)}.pdf`);
    });
  }
}
