export const renderTitle = (container, titleText) => {
  // title
  const title = container
    .append('div')
    .style('position', 'absolute')
    .style('pointer-events', 'none')
    .style('color', '#333')
    .style('text-decoration', 'underline')
    .style('top', '10px')
    .style('left', '0')
    .style('right', '0')
    .style('text-align', 'center')
    .style('font-size', '20px')
    .html(titleText);
};

export const renderTtip = container => {
  return container
    .append('div')
    .style('position', 'absolute')
    .style('pointer-events', 'none')
    .style('padding', '0 5px')
    .style('border', '1px solid #333')
    .style('opacity', 0);
};

export const renderLegend = (container, series, colorScale) => {
  const legend = container
    .append('div')
    .style('position', 'absolute')
    .style('pointer-events', 'none')
    .style('left', '10px')
    .style('right', '10px')
    .style('bottom', '10px')
    .style('font-size', '10px');

  for (const _seria of series) {
    const item = legend
      .append('div')
      .style('display', 'inline-block')
      .style('margin-right', '10px');
    item
      .append('div')
      .style('display', 'inline-block')
      .style('margin-right', '3px')
      .style('background', colorScale(_seria))
      .style('outline', '1px solid #888')
      .style('width', '10px')
      .style('height', '10px');
    item
      .append('div')
      .style('display', 'inline-block')
      .html(_seria);
  }
};
