import { Component, Input } from '@angular/core';
import * as d3 from 'd3';
import { max } from 'd3-array';
import { select } from 'd3-selection';
import flatten from 'lodash-es/flatten';
import uniq from 'lodash-es/uniq';
import { dimpleColors, renderLegend, renderTitle, renderTtip } from '../charts';
import { AbstractChartComponent } from '../charts.component';
@Component({
  selector: 'app-normalstacked',
  templateUrl: '../charts.component.html',
  styleUrls: ['../charts.component.css'],
})
export class NormalstackedchartComponent extends AbstractChartComponent {
  @Input()
  title = 'Normalized Stacked Chart';

  renderChart(titleText, data, containerElement) {
    renderChart(titleText, data, containerElement);
  }
}

function renderChart(titleText, data, containerElement) {
  if (!data) {
    return;
  }
  const container = select(containerElement);
  const categories = uniq(data.map(r => r.category));
  const series = uniq(data.map(r => r.series));
  const data2 = categories.map(category =>
    series.reduce(
      (i, _seria: string) => ({
        [_seria]: data.find(
          record => record.category === category && record.series === _seria
        ).value,
        ...i,
      }),
      { category }
    )
  );
  const stack = d3
    .stack()
    .keys(series)
    .order(d3.stackOrderNone)
    .offset(d3.stackOffsetExpand);
  const layers = stack(data2);
  const dataMax = +max(flatten(layers.map(d => d.map(e => e[1]))));

  const { width, height } = containerElement.getBoundingClientRect();

  const colorScale = d3.scaleOrdinal(dimpleColors);
  const xScale = d3
    .scaleBand()
    .domain(categories)
    .rangeRound([50, width - 50])
    .padding(0.1);
  const yScale = d3
    .scaleLinear()
    .domain([0, dataMax])
    .range([height - 80, 50]);

  const svg = container
    .append('svg')
    .style('border', '1px solid #ccc')
    .style('font-family', 'sans-serif')
    .style('font-size', '12px')
    .attr('width', width)
    .attr('height', height);

  svg
    .append('g')
    .classed('y-axis', true)
    .attr('transform', `translate(${xScale.range()[0]},0)`)
    .call(d3.axisLeft(yScale).tickFormat(d3.format('.0%')));

  svg
    .append('g')
    .classed('x-axis', true)
    .attr('transform', `translate(0,${yScale.range()[0]})`)
    .call(d3.axisBottom(xScale));

  svg
    .selectAll('g.y-axis g.tick')
    .append('line')
    .style('stroke', '#ddd')
    .attr('x1', 0)
    .attr('y1', 0)
    .attr('x2', xScale.range()[1] - xScale.range()[0])
    .attr('y2', 0);

  svg
    .selectAll('g.x-axis g.tick text')
    .attr('text-anchor', 'end')
    .attr('alignment-baseline', 'baseline')
    .attr('dx', '-5')
    .attr('transform', 'rotate(-30)');
  // .attr('transform', 'translate(-5, 0) rotate(-30)');

  const seria = svg
    .selectAll('g.seria')
    .data(layers)
    .enter()
    .append('g')
    .classed('seria', true)
    .style('fill', d => colorScale(d.key));

  seria
    .selectAll('rect.bar')
    .data(d => d)
    .enter()
    .append('rect')
    .classed('bar', true)
    .style('outline', '1px solid #888')
    // @ts-ignore
    .attr('x', d => xScale(d.data.category))
    .attr('y', d => yScale(d[1]))
    .attr('height', d => yScale(d[0]) - yScale(d[1]))
    .attr('width', xScale.bandwidth());

  for (const layer of layers) {
    for (const point of layer) {
      svg
        .append('text')
        .style('text-anchor', 'middle')
        // @ts-ignore
        .attr('x', xScale(point.data.category) + xScale.bandwidth() / 2)
        .attr('y', yScale(point[1]))
        .text(`${Math.round(point[1] * 100)}%`);
    }
  }

  seria
    .selectAll('rect.bar')
    .on('mousemove', function(d: any, i, j) {
      // @ts-ignore
      const _seria = d3.select(j[i].parentNode).datum().key;
      // @ts-ignore
      const mouse = d3.mouse(svg.node());
      ttip
        .style('opacity', 0.9)
        .style('left', `${mouse[0]}px`)
        .style('bottom', `${height - mouse[1]}px`)
        .style('background', `${colorScale(_seria)}`)
        // @ts-ignore
        .html(
          [d.data.category, _seria, `${Math.round((d[1] - d[0]) * 100)}%`].join(
            '<br>'
          )
        );
    })
    .on('mouseleave', function() {
      ttip.style('opacity', 0);
    });

  // svg
  //   .selectAll('rect.bar')
  //   .on('mousemove', function(d, i, j) {
  //     const mouse = d3.mouse(svg.node());
  //     ttip
  //       .style('opacity', 0.9)
  //       .style('left', `${mouse[0]}px`)
  //       .style('bottom', `${height - mouse[1]}px`)
  //       .style('background', `${colorScale(d.category)}`)
  //       .html(Object.values(d).join('<br>'));
  //   })
  //   .on('mouseleave', function() {
  //     ttip.style('opacity', 0);
  //   });

  // tooltip
  const ttip = renderTtip(container);
  // title
  renderTitle(container, titleText);

  // legend
  renderLegend(container, series, colorScale);
}
